import React, { Component } from 'react'
import $ from 'jquery'
import url from './audio/How Long Will I Love You.mp3' // 引入背景音乐文件
import myImage from './images/1.jpg'

class Main extends Component {
    state = {
        date: {},
    }
    componentDidMount() {
        this.print();
        setInterval(() => {
            this.time(2020, 6, 6) // 你们的纪念日
        }, 1000
        )
        var audio = document.getElementById("audio");
        setTimeout(() => audio.play(), 1000) // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
    }
    print = () => {
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args = arguments;
                var current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn, 200)
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }
    time = (year, month, day) => {
        var dateNow = new Date();
        var dateJNR = new Date(year, month - 1, day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000));
        var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24);
        var minute = parseInt((dateNow - dateJNR) / (1000 * 60) % 60);
        var second = parseInt((dateNow - dateJNR) / 1000 % 60);
        this.setState({ date: { d: d, hour: hour, minute: minute, second: second } });
    };
    render() {
        const date = () => {
            if (this.state.date.d !== undefined) {
                const { d, hour, minute, second } = this.state.date
                return (<p>我们已经在一起: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return (
            <div className="App animated bounceInLeft">
                <div className="date">{date()}</div>
                <div id="autotype">
                    <h1 style={{ fontWeight: 900 }}>我最最善良美丽的婷宝宝</h1>
                    <p>男朋友错啦。</p>
                    <p>马上我们就要在一起一年啦，我还在悄悄准备这个日子呢，却把最应该做的事情没有做。</p>
                    <p>你做手术这么大的事情，我没陪着也就罢了，还自以为是的以为应该不是什么大问题。</p>
                    <p>这几天我因为不知道你生气的原因，也挺莫名其妙的心里不舒坦的。每天都是带着闷气睡觉。估计你更加是。</p>
                    <p>但是昨晚听了你说的原因，我觉得没什么好说的，这事就是错在我。</p>
                    <p>现在心里飘了，对你不够上心了。关心少了，记性差了，太不是男人了。</p>
                    <p>虽然疼我不能替你疼，但是爱情最重要的就是陪伴呀，跟着我你也挺不容易的。我还连陪伴都没做好。</p>
                    <p>我的婷宝宝最好看，最善良，最懂事，最可爱，最体贴人，最讲理，最以理服人。当然也最爱我。
                </p>
                    <p>啥也别说了。等你从襄阳回来，就是端午节了，12号带你去长沙喝奶茶。要是你想先看猫，就先看猫。
                </p>
                    <p>这当然不够认错的态度。接下来一个月，啥都听你的，你想吃什么我都一起吃，想去哪我都陪着。从今天到端午节前都不"欺负"婷宝宝。
                </p>
                    <p>男朋友知道错了。我不是不会哄人，我就是这两天不知道你生气的点，无从下手。但是苗大爷是最爱你的。Yeah</p>
                    <div style={{ textAlign: 'right' }}>
                        <p>爱你的苗大爷</p>
                        <p>2021年5月28日</p>
                    </div>
                </div>
                <img className='img' src={myImage} width={300} height={300}></img>
                <audio id="audio" src={url}></audio>
            </div >

        )
    }
}
export default Main


